  text-align: center;
  height: 40px;
  color: #9B536F;
  line-height: 40px;
  padding: 0 12px;

  &:hover {
    background: #9B536F;
    color: #ffffff;
  }

  &.ac {
    background: #9B536F;
    color: #ffffff;
  }
}
</style>
