/**
 * @name deleteById
 * @desc 默认删除接口，传入删除得表名和id
 * */
function deleteById(
    tableName: string,
    id: string,
    pageParams: any,
    url?: any
): Promise<boolean> {
    return new Promise((resolve) => {
        confirm().then((r) => {
            if (r) {
                post(url || `/${tableName}/deleteById`, {id: id}).then((res) => {
                    if (res.success) {
                        msg(res.msg)
                        pageParams.refresh = true
                        resolve(true)
                    } else {
                        msgError(res.msg)
                        resolve(false)
                    }
                })
            }
        })
    })
}

/**
 * @name delMultiple
 * @desc 根据id批量删除，传入section
 * */
function delMultiple(
    tableName: string,
    selection: any,
    pageParams: any,
    message = '请至少选择一条数据'
): Promise<boolean> {
    return new Promise((resolve) => {
        confirm().then((r) => {
            if (r) {
                let str = ''
                if (typeof selection !== 'string') {
                    if (selection.length === 0) {
                        tools.msgError(message)
                        return
                    }
                    selection.forEach((res: any, index: number) => {
                        str += index === selection.length - 1 ? res.id : res.id + ','
                    })
                } else {
                    str = selection
                }
                tools.post(`/${tableName}/deleteMultiple`, {id: str}).then((res) => {
                    if (res.success) {
                        msg(res.msg)
                        pageParams.refresh = true
                        resolve(true)
                    } else {
                        msgError(res.msg)
                        resolve(false)
                    }
                })
            }
        })
    })
}

/**
 * @name openDrawer
 * @desc 抽屉新增或者修改执行;
 * */
function openDrawer(drawer: any, isEdit?: boolean, formList?: any, val?: any) {
    drawer.isDrawer = true
    if (isEdit) {
        drawer.isEdit = true
        formList.forEach((res: any) => {
            res.value = val[res.key]
        })
    } else {
        drawer.isEdit = false
    }
}

