function numberToLetter(value: any) {
    return String.fromCharCode(64 + parseInt(String(value), 10))
}

function loading(is = true, text = '请稍等...') {
    if (!is) {
        loadingData.close()
        return
    }
    loadingData = ElLoading.service({
        lock: true,
        text: text,
        background: 'rgba(0, 0, 0, 0.7)'
    })
}

function go(url, data?, cb?) {
    router.push({
        name: url,
        params: data || {}
    }).then(() => {
        cb && cb()
    })
}

const day = ((i) => {
    const d = new Date(i).getDate()
    return d > 9 ? d : '0' + d
})
const yearMouth = ((i) => {
    const y = new Date(i).getFullYear()
    const m = (new Date(i).getMonth() + 1) > 9 ? new Date(i).getMonth() + 1 : '0' + (new Date(i).getMonth() + 1)
    return `${y}-${m}`
})
const sortFn = (arr, flag) => {
    arr.sort((a, b) => {
        if (flag) {
            return b.sequence - a.sequence
        }else{
            return a.sequence - b.sequence
        }
    })
    return arr
}

const ellipsis = (value, len?) => {
    value = value.replace(/<\/?.+?>/g, "").replace(/ /g, "").replace(/&nbsp;/ig, "")      //把v-html的格式标签替换掉
    if (value.length > len) {
        return value.slice(0, len) + "...";
    } else {
        return value
    }
}

const showIsLogin = () => {
    ElMessageBox.confirm(
        '请先登录!',
        '温馨提示:',
        {
            confirmButtonText: '去登陆',
            cancelButtonText: '取消',
            type: 'warning',
        }
    )
        .then(() => {
            go('Login')
        })
        .catch(() => {
            return
        })
}
const showPdf = (data,target='path') => {
    if(data[target]){
        const file = data[target].split(',')
        if(file.length === 1 && file[0].search('.pdf')>-1){
            return true
        }else{
            return false
        }
    }
}
const returnMenu = (data, str) => {
    let arr = []
    data.forEach(item => {
        if (item.name === str) {
            arr = item
        }
    })
    return arr
}
const returnIndex = (data, str) => {
    let ind = 0
    data.forEach((item,index) => {
        if (item.name === str) {
            ind = index
        }
    })
    return ind
}
const tools = {
    post,
    get,
    msg,
    msgError,
    confirm,
    formatTime,
    // loadScript,
    loading,
    deleteById,
    delMultiple,
    go,
    showIsLogin,
    // imgError,
    openDrawer,
    numberToLetter,
    day,
    yearMouth,
    sortFn,
    ellipsis,
    showPdf,
    returnMenu,
    returnIndex,
    limit: 20,
    props: {
        checkStrictly: true,
        label: 'name',
        value: 'id'
    }
}

export default tools
